.popup {
    display: flex;
    visibility: hidden;
    justify-content: center;
    margin: 0;
    width: 100%;
    height: 100%;
    transition: visibility 0.3s, opacity 0.3s linear;
    opacity: 0;
    background-color: rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    z-index: 1;
}
