.profile__avatar {
  border-radius: 50%;
  width: 120px;
  height: 120px;
  object-fit: contain;
  transition: opacity 0.3s ease;
}

.profile__avatar:hover {
  opacity: 0.6;
  cursor: pointer;
}

@media screen and (max-width: 896px) {
  .profile__avatar {
      align-self: center;
  }
}